(function ($, undefined) {
    /* smooth scroll */
    $('a.js-smooth-scroll').click(function () {
        $('html, body').animate({
            scrollTop: $($.attr(this, 'href')).offset().top
        }, 500);
        return false
    })

    console.log(2);
})(jQuery);


$(function () {
    new Swiper(".cards-swiper", {
        slidesPerView: 3,
        speed: 3000,
        loop: true,
        autoplay: {
            delay: 2000,
            disableOnInteraction: false
        },
        pagination: {
            el: ".cards-swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
                spaceBetween: 200,
            },
            554: {
                slidesPerView: 2,
                spaceBetween: 10,
            },
            768: {
                slidesPerView: 2,
                spaceBetween: 75,
            },
            992: {
                slidesPerView: 3,
                spaceBetween: 25,
            },
            1400: {
                slidesPerView: 4,
                spaceBetween: 25,
            }
        }
    });
});
